/* Flavor Tree Explorer Page - Full Screen */
.flavor-tree-explorer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.error-message {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--muted);
  text-align: center;
}

.error-message h2 {
  color: var(--green-900);
  margin-bottom: 8px;
}

